export default {
	data() {
		return {
			list: [],
			BSSID: '',
			tiem: ''
		}
	},
	onLoad() {
		wx.startWifi({
		  success (res) {
			  wx.getWifiList({
				  success (e) {
				  	console.log(e)
				  }
			  })
			console.log(res.errMsg)
		  }
		})
	},
	methods: {
		jlWifi (data) {
			this.BSSID = data.BSSID
			uni.showToast({
				title: '记录wifi：' + data.SSID,
				icon: 'none',
				duration: 4000
			})
			let This = this
			this.tiem = setInterval(() => {
				console.log('每隔一分钟检测一次')
				This.startSearch()
				let off = true
				This.list.forEach((el, index) => {
					if (el.BSSID === This.BSSID) {
						off = false
					}
				})
				if (off) {
					console.log('没有wifi')
					setInterval(() => {
						uni.vibrateLong({
							success: function () {
								console.log('success');
							}
						})
					}, 500)
				}
			},10000)
		},
		startSearch() {
			const getWifiList = () => {
				wx.getWifiList({
					success: (res) => {
						console.log(res)
						wx.onGetWifiList((res) => {
							console.log(res)
							const wifiList = res.wifiList
							.sort((a, b) => b.signalStrength - a.signalStrength)
							.map(wifi => {
								const strength = Math.ceil(wifi.signalStrength * 4)
								return Object.assign(wifi, { strength })
							})
							this.list = res.wifiList
						})
					},
					fail(err) {
						console.error(err);
					}
			  })
			}

			const startWifi = () => {
				wx.startWifi({
					success: getWifiList,
					fail(err) {
						console.error(err);
					}
				})
			}

			wx.getSystemInfo({
				success(res) {
					const isIOS = res.platform === 'ios'
					if (isIOS) {
						wx.showModal({
							title: '提示',
							content: '由于系统限制，iOS用户请手动进入系统WiFi页面，然后返回小程序。',
							showCancel: false,
							success() {
								startWifi()
							}
						})
						return
					}
					startWifi()
				}
			})
		},
		stopSearch() {
			wx.stopWifi({
				success(res) {
					console.log(res);
				},
				fail(err) {
					console.error(err);
				}
			})
		}
	}
}
